footer{
	padding-top: 9rem;
	color:#fff;
	position: relative;
	h1{
		font-weight: 800;
		font-size: 2.5rem;
		margin-bottom: 0.9rem;
		
	}
	@media(max-width:480px){
		padding-top: 4rem;

		h1{
		font-size: 1.8rem;

		}
	}
	.desc{
		margin-bottom: 4rem;
	}
	.copyright{
	text-align: center;
	padding-top: 5rem;
	padding-bottom: 2rem;
	}
	.button-input{
		color:#fff;
		height: 45px;
	}
	.footerDetails{
		display: flex;
		margin-bottom: 1.5rem;
		padding-left: 1rem;
		align-items: flex-start;
		svg{
			margin-right: 1rem;
		}
	}

}
.footer::after{
	
	background-image: url("../../images/apply-bg.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	position: absolute;
	background-attachment: fixed;
	right: 0;
	opacity: 1;
	top: 0;
	height: 100%;
	z-index: -1;
	width: 100%;
	content:"";
}