.product{
	padding-top: 20rem;
	margin-top: 5rem;
	position: relative;
	h2{
		margin-bottom: 1rem;
    font-size: 2.4rem;
    font-weight: 900;
    text-align: center;
	
	}
	@media(max-width:768px){
		padding-top: 5rem;
	}
	@media(max-width:480px){
		padding-top: 5rem;
		h2{
		font-size: 1.8rem;
		}
	}
	p{
		margin-bottom: 5rem;
	}
	.product-left{
		position: absolute;
		left: 0;
		top: 0;
		@media(max-width:640px){
			display: none;
		}
	}
	.grid {
		margin: 0 auto;
		list-style: none;
		text-align: center;
		li {
			display: inline-block;
			margin: 0;
			padding: 20px;
			text-align: left;
			position: relative;
		}
		figure {
			margin: 0;
			position: relative;
			img {
				width: 100%;
				max-width: 100%;
				display: block;
				position: relative;
			}
		}
		.detailsBtn{
			position: absolute;
			right:20px;
			border-radius: 4px;
			padding:10px 20px;
			bottom:20px;
		}
		figcaption {
			position: absolute;
			top: 0;
			left: 0;
			padding: 42px 22px;
			background: #f2f2f2;
			color: #000;
			h1 {
				font-size: 1.6rem;
				line-height: 2.2rem;
				margin: 0;
				padding-bottom: 12px;
				color: var(--themeMainColor);
				@media(max-width:480px){
					font-size: 1rem;
					line-height: 1.4rem;

				}
			}
			small {
				font-size: 16px;
				letter-spacing: 1px;
				line-height: 1.6;
				@media(max-width:480px){
					font-size: 0.9rem;
					line-height: 1rem;

				}
			}
		}
	}
	
	.animateGrid li {
		-webkit-perspective: 1700px;
		-moz-perspective: 1700px;
		perspective: 1700px;
		-webkit-perspective-origin: 0 50%;
		-moz-perspective-origin: 0 50%;
		perspective-origin: 0 50%;
	}
	
	.animateGrid figure {
		-webkit-transform-style: preserve-3d;
		-moz-transform-style: preserve-3d;
		transform-style: preserve-3d;
	}
	
	.animateGrid figure > div {
		overflow: hidden;
	}
	
	.animateGrid figure img {
		-webkit-transition: -webkit-transform 0.4s;
		-moz-transition: -moz-transform 0.4s;
		transition: transform 0.4s;
	}
	
	.animateGrid figure:hover img,
	.animateGrid figure.cs-hover img {
		-webkit-transform: translateX(25%);
		-moz-transform: translateX(25%);
		-ms-transform: translateX(25%);
		transform: translateX(25%);
	}
	
	.animateGrid figcaption {
		height: 100%;
		width: 50%;
		opacity: 0;
		-webkit-backface-visibility: hidden;
		-moz-backface-visibility: hidden;
		backface-visibility: hidden;
		-webkit-transform-origin: 0 0;
		-moz-transform-origin: 0 0;
		transform-origin: 0 0;
		-webkit-transform: rotateY(-90deg);
		-moz-transform: rotateY(-90deg);
		transform: rotateY(-90deg);
		-webkit-transition: -webkit-transform 0.4s, opacity 0.1s 0.3s;
		-moz-transition: -moz-transform 0.4s, opacity 0.1s 0.3s;
		transition: transform 0.4s, opacity 0.1s 0.3s;
		@media(max-width:480px){
			width: 70%;
		}
	}
	
	.animateGrid figure:hover figcaption{
		opacity: 1;
		-webkit-transform: rotateY(0deg);
		-moz-transform: rotateY(0deg);
		transform: rotateY(0deg);
		-webkit-transition: -webkit-transform 0.4s, opacity 0.1s;
		-moz-transition: -moz-transform 0.4s, opacity 0.1s;
		transition: transform 0.4s, opacity 0.1s;
	}
	.animateGrid figure.cs-hover figcaption {
		opacity: 1;
		-webkit-transform: rotateY(0deg);
		-moz-transform: rotateY(0deg);
		transform: rotateY(0deg);
		-webkit-transition: -webkit-transform 0.4s, opacity 0.1s;
		-moz-transition: -moz-transform 0.4s, opacity 0.1s;
		transition: transform 0.4s, opacity 0.1s;
	}
	
	.animateGrid figcaption a {
		position: absolute;
		bottom: 20px;
		right: 20px;
	}
}


@media (max-width: 980px) {
	.grid figcaption h1 {
		font-size: 1rem;
		line-height: 1.6rem;
	}
	.grid figcaption {
		padding: 22px 18px;
	}
}

@media screen and (max-width: 31.5em) {
	.grid {
		padding: 10px 10px 100px 10px;
	}
	.grid li {
		width: 100%;
		min-width: 300px;
	}
}
