.team{
	padding-top:8rem;
	padding-bottom: 8rem;
	.teamDesc{
		h2{
			margin-bottom: 1rem;
			font-size: 2.5rem;
			font-weight: 900;
			text-align: center;
			
		}
		p{
			margin-bottom: 6rem;
			line-height: 2;
			font-size: 1rem;
			text-align: center;
		}
	}
	@media(max-width:480px){
		padding-top: 4rem;
		.teamDesc{
			h2{
			font-size: 1.8rem;
			}
		}
		
	}
.team-thumb {
	width: 100%;
	height: 400px;
	margin: 32px 0;

	text-align: center;
	background: #ffffff;
	border-radius: 4px;
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
	.image-holder {
		position: relative;
		top: 40px;
		display: inline-block;
		margin-bottom: 40px;
		padding: 10px;
		-webkit-transition: all 0.3s ease;
		transition: all 0.3s ease;
		img{
			display: block;
    max-width: 100%;
    height: auto;
	border-radius: 50%;
		}
	  }
	  .heading {
		font-weight: bold;
		position: relative;
		top: 10px;
		-webkit-transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
		transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
	  }
	  &:hover{
		background-color: var(--secondaryColor);
		.image-holder {
			top: -40px;
			margin-bottom: 0px;
		  }
		  .heading {
			top: -10px;
		  }
		  .description {
			opacity: 1;
			-webkit-transform: scale(1);
			-ms-transform: scale(1);
			transform: scale(1);
		  }
		  p.description {
			color: var(--secondaryFontColor);
		  }
		  
	  }
	  .description {
		font-size: 18px;
		width: 80%;
		margin: 0 auto;
		opacity: 0;
		-webkit-transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
		transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
		-webkit-transform: scale(0);
		-ms-transform: scale(0);
		transform: scale(0);
	  }
  }
}