/* Slider */
.slick-slider {
	position: relative;

	display: block;
	box-sizing: border-box;

	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

	-webkit-touch-callout: none;
	-khtml-user-select: none;
	-ms-touch-action: pan-y;
	touch-action: pan-y;
	-webkit-tap-highlight-color: transparent;
}

.slick-list {
	position: relative;
	display: block;
	overflow: hidden;
	padding-bottom: 1rem !important;
	margin: 0;
	padding: 0;
}

.slick-list:focus {
	outline: none;
}

.slick-list.dragging {
	cursor: pointer;
	cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
	-webkit-transform: translate3d(0, 0, 0);
	-moz-transform: translate3d(0, 0, 0);
	-ms-transform: translate3d(0, 0, 0);
	-o-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

.slick-track {
	position: relative;
	top: 0;
	left: 0;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.slick-track:before,
.slick-track:after {
	display: table;
	content: '';
}

.slick-track:after {
	clear: both;
}

.slick-loading .slick-track {
	visibility: hidden;
}

.slick-slide {
	display: none;
	float: left;
	height: 100%;
	min-height: 1px;
}

[dir='rtl'] .slick-slide {
	float: right;
}

.slick-slide img {
	display: block;
}

.slick-slide.slick-loading img {
	display: none;
}

.slick-slide.dragging img {
	pointer-events: none;
}

.slick-initialized .slick-slide {
	display: block;
}

.slick-loading .slick-slide {
	visibility: hidden;
}

.slick-vertical .slick-slide {
	display: block;
	height: auto;
	border: 1px solid transparent;
}

.slick-arrow {
	display: none !important;
}




 
.slick-dotted.slick-slider {
	margin-bottom: 30px;
}

.slick-dots {
	display: block;
	position: absolute;
	bottom: -50px;
	width: 100%;
	padding: 0;
	margin: 0;
	list-style: none; 
	text-align: center; 	
}

.slick-dots li {
	position: relative;
	display: inline-block; 
	width: 1rem; 
	height: 1rem;
	margin: 0 3px;
	padding: 0;
	cursor: pointer;
}

.slick-dots li button {
	font-size: 0;
	line-height: 0;
	display: block;
	width: 1rem;
	height: 1rem;
	padding: 5px; 
	cursor: pointer;
	color: transparent;
	border: 0;
	outline: none;
	border-radius: 50%;
	background: var(--primaryColor);
}

.slick-dots li button:hover,
.slick-dots li button:focus {
	outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
	opacity: 1;
}

.slick-dots li button:before {  
	position: absolute;
	top: 0;
	left: 0; 
	opacity: 0.25; 
    width: 1rem;
    height: 1rem; 
	content: "";
    cursor: pointer;
    color: transparent;
    border: 0;
    outline: none;
    background: var(--themeMainColor);
    border-radius: 1.2rem;
	@media(max-width:768px){
		height: 1rem;
		width: 1rem;
	}
}

.slick-dots li.slick-active button:before {
	opacity: .75;
	color: var(--themeMainColor);
	background-color: var(--themeMainColor);
}