.partner{
	background-color: var(--secondaryColor);
	padding-top:7rem;
	padding-bottom: 2rem;
	position: relative;
	.partnerGrid{
		display:flex;
		flex-wrap: wrap;
		justify-content: space-between;
		@media(max-width:768px){
			justify-content: center;
		}
		.partnerImg{
			height: 8rem;
			margin:1rem 1rem 3rem;
			filter: grayscale(1.4);
			width: 15%;
			@media(max-width:768px){
				height:7rem;
				width: 25%;
			}
			&:hover{
				filter: none;
			}
			img{
				width: 100%;
				max-height: 100%;
				display: block;
			}
			
			@media(max-width:640px){
				width: 100%;
				img{
					width: 75%;
					margin:0 auto;
				}
			}
		}
	}
}