.service{
	background-color: var(--primaryColor);
	padding-top: 7rem;
	perspective: 1000px;
	padding-bottom: 5rem;
	margin-top: 4rem;
	color:var(--primaryFontColor);
	h2{
		margin-bottom: 1rem;
		font-size: 2.4rem;
		font-weight: 900;
		text-align: center;
		@media(max-width:480px){
			font-size: 1.8rem;
		}
	}
	.serviceDesc{
		margin-bottom: 6rem;
		line-height: 2;
		font-size: 1rem;
		text-align: center;
	}
	.serviceWrapper{
		text-align: center;
		height: 100%;
		border-radius: 10px;
		box-shadow: 0px 0px 15px #f1f1f1;
		background-color: #fff;
		padding: 30px;
		transition: 0.2s;
		
		&:hover{
			background-color: #000;
			color:#fff;
			.serviceImg{
				border-bottom: 1px solid #fff;
			}
		}
		h5{
			font-weight: 600;
			font-size: 1.2rem;
			line-height: 1.5;
			margin-bottom: 2rem;
		}
		.serviceImg{
			padding-bottom: 2rem;
			margin-bottom: 2rem;
			border-bottom: 1px solid var(--secondaryFontColor);
		}
		p{
			line-height: 1.5;
			font-size: 1rem;

		}
	}
	@media(max-width:768px){
		.serviceWrapper{
			margin-bottom: 2rem;
		}
	}
	@media(max-width:480px){
		.serviceWrapper{
			margin-bottom: 2rem;
		}
		padding-top: 0rem;
		margin-top: 0rem;
	}

}
