@import "./bootstrapGrid";
@import "./slickStyle";
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Inter:wght@200;300;500;600&family=Poppins:wght@700;900&display=swap');

*{
	box-sizing: border-box;
	padding: 0;
	margin:0;
}
body{
	font-family: 'Montserrat', sans-serif;
	padding:0;
	margin:0;
	overflow-x: hidden;
	letter-spacing: 1px;
	background-color: var(--mainBgColor);
}
html {
	scroll-behavior: smooth;
  }
.container{
	max-width: 1200px;
	margin:0 auto;
	@media(max-width:1024px){
		max-width: 980px;
	}
	@media(max-width:768px){
		max-width: 700px;
	}
	@media(max-width:640px){
		max-width: 580px;
	}
	@media(max-width:425px){
		max-width: 90%;
	}
}
.mainHeading{
	font-size: 2.5rem;
}
.primaryBtn{
	background-color: var(--themeMainColor);
	color:var(--themeFontColor);
	font-size: 14px;
	cursor: pointer;
	padding:1rem 2rem;
	font-family: 'Montserrat', sans-serif;
	border: none;
	outline: none;
	letter-spacing: 1px;
	border-radius: 3rem;
}

button{
	cursor: pointer;
	&:focus{
		outline: none;
	}
}
p{
	font-size: 14px;
}
a{
	text-decoration: none;
}
li{
	list-style-type: none;
}

input {
	width: 100%;
	height: 40px;
	border-radius: 4px;
	background-color: transparent;
	border:1px solid #f0f0f0;
	outline: none;
	box-shadow: none;
	font-size: 13px;
	font-weight: 500;
	color: #f0f0f0;
	padding: 0px 15px;
	margin-bottom: 30px;
	letter-spacing: 1px;
	&::placeholder{
		color:#f2f2f2;
	}

  }
  textarea {
	  letter-spacing: 1px;
	width: 100%;
	min-height: 140px;
	max-height: 180px;
	width: 100%;
	border-radius: 4px;
	background-color: transparent;
	outline: none;
	border: 1px solid #f0f0f0;
	box-shadow: none;
	font-size: 13px;
	font-weight: 500;
	color: #f0f0f0;
	padding: 15px;
	margin-bottom: 30px;
	&::placeholder{
		color:#f2f2f2;
	}
  }