.headerWrapper{
	color:#000;
	transition: 0.3s;
	background-color: #fff;
	width: 100%;
	padding:1.5rem 0rem;
	top:0;
	z-index: 100;
	position: fixed;
	.logo{
		font-weight: 900;
		font-size: 1.5rem;
	}
	&.stickyHeader{
		position: fixed;
		width: 100%;
		box-shadow: 0px 0px 4px #e2e2e2;
		animation:moveBottom 0.3s ease;
	}
	.menu{
		display: flex;
		justify-content: flex-end;
		.deskMenu{
			display: flex;
			@media(max-width:1024px){
				display: none;
			}
			li{
				list-style-type: none;
				cursor: pointer;
				transition: 0.3s all;
				padding:0.5rem 1rem;
				&:hover{
					background-color: var(--themeMainColor);
					color:var(--themeFontColor);
					border-radius: 0.3rem;
				}
			}
		}
		.mobMenu{
			display: none;
			@media(max-width:1024px){
				display: block
			}
			.iconLinkBtn{
				background-color: transparent;
				border: none;
				svg{
					color:#000;
				}
			}
		}
		.mobMenuList{
			position: fixed;
			width: 100%;
			left:0;
			top:4rem;
			background-color: var(--primaryColor);
			color:var(--primaryFontColor);
			z-index:1;
			padding-top: 1rem;
			padding-bottom: 1rem;

			li{
				list-style-type: none;
				padding:0.5rem 1.5rem;
				&:hover{
					background-color: var(--themeMainColor);
					color:var(--themeFontColor);
				}
			}
			
		}
	}
}

@keyframes moveBottom{
	0%{
		transform: translateY(-80px);
	}
	100%{
		transform: translateY(0px);

	}
}