.heroSection{
	position: relative;
	background-color: var(--primaryColor);
	color:var(--primaryFontColor);
	position: relative;
	padding-top:20rem;
	@media(max-width:768px){
		padding-top: 13rem;
	}
	@media(max-width:480px){
		padding-top: 10rem;
	}
	.mainHeading{
		line-height: 1.5;
		font-weight: 900;
		font-size: 2.5rem;
		@media(max-width:480px){
			font-size: 1.8rem;
		}
	}
	p{
		margin-top: 1rem;
		line-height: 2;
	}
	button{
		margin-top: 1.5rem;
	}
	
}
.heroSection::after{
	
		background-image: url("../../images/banner-right-image.png");
		background-repeat: no-repeat;
		background-size: cover;
		position: absolute;
		right: 0;
		top: 100px;
		width: 50%;
		content:"";
		height: 800px;
		@media(max-width:768px){
			display: none;
		}
}