.aboutUs{
	background-color: var(--primaryColor);
	color:var(--primaryFontColor);
	padding-bottom: 8rem;
	padding-top: 4rem;
	.descAbout{
		margin-top: 1rem;
	}
	figure{
		img{
			width: 100%;
			overflow: hidden;
		}
	}
	@media(max-width:768px){
		padding-bottom: 4rem;
	}
	.aboutDesc{
		position: relative;
		height: 100%;
		flex-direction: column;
		background-color: var(--primaryColor);
		display: flex;
	
		justify-content: center;
		height: 100%;
		padding:4rem;
		@media(max-width:480px){
			padding:2rem;
		}
		h2{
			font-weight: 900;
			font-size: 2.5rem;
			@media(max-width:480px){
				font-size: 1.8rem;
			}
		}
		p{
			line-height: 1.6;
			font-size: 16px;
			margin-top: 2rem;
		}
	}
	.aboutDetails{
		margin-top: 3rem;
		h4{
			color:var(--themeMainColor);
			font-size: 1.5rem;
		}
		h2{
			margin-top: 0.4rem;
			font-weight: 800;
			font-size: 3rem;
		}
	}
	
}

